<template>
	<view>
		<view v-for="(item, index) in orderList" :key="index" class="order-item-box">
			<!-- 订单号区域 -->
			<view class="order-title-box" @click="handleGoToDetail(index)">
				<!-- 订单号 -->
				<view class="order-title">
					<view class="rectangle"></view>
					<text selectable class="order-number">{{i18n.collectOrderNo}}：{{item.collectOrderNo}}</text>
				</view>
				<!-- 箭头 -->
				<view class="navi-right">
					<view class="iconfont iconjiantou"></view>
				</view>
			</view>
			<!-- 中间区域 -->
			<view class="order-main">
				<view class="order-main-top">
					<view class="order-state" :style="item.status !== 12600 && item.status !== 12800 ? 'color: #FFB32F;' : 'color: #23252B'">
						<!-- 已接单 -->
						<template v-if="item.status === 12600">{{i18n.receiving}}</template>
						<!-- 取消揽件 -->
						<template v-else-if="item.status === 12900">{{i18n.cancelSolicitation}}</template>
						<!-- 完成揽件 -->
						<template v-else-if="item.status === 12800">{{i18n.completeContract}}</template>
						<!-- 揽件失败 -->
						<template v-else>
							<view>
								<view>
									{{i18n.abnormal}}
									<text class="iconfont iconic_warning"></text>
								</view>
								<view class="abnormal-reason">{{item.remark}}</view>
							</view>
						</template>
					</view>
					<!-- 代收货款暂时不用 -->
					<!-- <view class="order-price-box">
						<view v-if="item.isCollection === 1" class="order-price">
							S$<text>{{item.codAmount}}</text>
						</view>
						<view class="price-tip">
							{{i18n.paymentForGoods}}
						</view>
					</view> -->
				</view>
				<!-- 用户信息区域 -->
				<view class="order-main-middle">
					<view class="user-message">
						<view class="user-name-box">
							<!-- 用户头像 -->
							<view class="user-icon">
								<image mode="widthFix" src="../../static/img/user-icon.png"></image>
							</view>
							<view class="user-name">{{item.sender}}</view>
							<!-- 电话 -->
							<text @click="handlePhoneCall(item.senderContact)" class="user-phone" selectable>{{item.senderContact}}</text>
						</view>
						<view class="order-user-code">{{i18n.postCode}}：{{item.senderZipCode}}</view>
					</view>
					<view class="user-address-box">
						<text class="user-address" selectable>{{item.senderAddr}}</text>
						<!-- 导航按钮 -->
						<view class="navigator-icon" @click="handleOpenGoogleMapApp">
							<image mode="widthFix" src="../../static/img/navigation.png"></image>
						</view>
					</view>
				</view>
				<!-- 细节信息 -->
				<view class="sending-message-box">
					<!-- 揽件时间段 -->
					<view class="sending-time">{{i18n.collectTimePeriod}} : {{item.collectTimePeriod}}</view>
					<!-- 预估重量 -->
					<view class="arrive-time">{{i18n.weight}} : {{item.weight}}kg</view>
				</view>
				<!-- 对快递员说 -->
				<view class="sending-message-box">
					<view class="sending-time">{{i18n.toCourier}} : {{item.toTheCourier}}</view>
				</view>
			</view>
			<!-- 查看详情 -->
			<view class="detail-tool-box">
				<view @click="handleGoToDetail(index)" class="navi-left">{{i18n.viewDetails}}</view>
				<!-- 揽件完成 -->
				<view @click="handleGoToDetail(index)" v-if="item.status === 12800" class="navi-right">
					<view class="iconfont iconjiantou"></view>
				</view>
				<!-- 其他状态有按钮 -->
				<view v-else class="status-btn-box">
					<!-- 揽件失败按钮 -->
					<navigator 
					hover-class="none" 
					:url="`/pages/order/order-fail/order-fail?waybillNumber=${item.waybillNumber}&subsection=collect`"
					class="status-btn status-btn_normal">
						{{i18n.abnormal}}
					</navigator>
					<!-- 揽件成功按钮 -->
					<navigator 
					hover-class="none" 
					:url="`/pages/collectorder/collectorder-success/collectorder-success?waybillNumber=${item.waybillNumber}`" 
					class="status-btn status-btn_success">
						{{i18n.successfulSolicitation}}
					</navigator>
				</view>
			</view>
		</view>
	</view>

</template>

<script>
	import { openGoogleMapAppMixin }  from '@/common/mixin.js'
	export default {
		mixins: [openGoogleMapAppMixin],
		props: {
			orderList: {
				type: Array,
				default() {
					return []
				}
			},
			iscomplete: {
				type: Boolean,
				default: false
			}
		},
		methods: {
			// 跳转到详情页
			handleGoToDetail(index) {
				// 传递到详情页的参数
				const orderDetailParams = {
					logisticsOrderId: this.orderList[index].logisticsOrderId
				}
				const orderDetail = encodeURIComponent(JSON.stringify(orderDetailParams))
				uni.navigateTo({
					url: `/pages/collectorder/collectorder-detail/collectorder-detail?orderDetail=${orderDetail}`
				})
			},
			// 拨打电话
			handlePhoneCall(phoneNumber) {
				uni.makePhoneCall({
					phoneNumber
				})
			}
		},
		computed: {
			// 国际化
			i18n() {
				return this.$t('order')
			}
		}
	}
</script>

<style lang="scss" scoped>
	.order-item-box {
		margin-bottom: 20rpx;
		position: relative;
		background: #FFFFFF;
		box-shadow: 0rpx 0rpx 10rpx 0rpx rgba(29, 30, 30, 0.04);
		border-radius: 10rpx;
		.order-title-box{
			display: flex;
			justify-content: space-between;
			align-items: center;
			.order-title {
				display: flex;
				align-items: center;
				height: 74rpx;
			
				.rectangle {
					width: 6rpx;
					height: 20rpx;
					background: $color;
				}
			
				.order-number {
					margin-left: 26rpx;
					padding: 26rpx 0;
					font-size: 22rpx;
					color: #23252B;
				}
			}
			.navi-right {
				padding-right: 30rpx;
				.iconfont {
					font-size: 23rpx;
					color: #B0B4BA;
				}
			}
		}
		
		.order-main {
			padding: 0rpx 30rpx 0;
			.order-main-top {
				border-top: 1rpx solid #F5F5F5;
				padding: 30rpx 10rpx 0;
				@include flex-between;
				align-items: center;
				.order-state {
					font-size: 28rpx;
					line-height: 1;
					font-weight: bold;
					color: #23252B;
					display: flex;
					align-items: center;
					.abnormal-reason{
						font-size: 22rpx;
						color: #23252B;
						margin-top: 16rpx;
						font-weight: normal;
					}
					.iconfont {
						margin-left: 8rpx;
						font-size: 24rpx;
					}
				}
				.order-price-box{
					display: flex;
					flex-direction: column;
					align-items: center;
					.order-price {
						color: #FC2F2F;
						font-size: 20rpx;
					
						text {
							font-size: 30rpx;
							font-weight: bold;
						}
					}
					.price-tip{
						font-size: 22rpx;
						font-weight: bold;
						color: #23252B;
						line-height: 33rpx;

					}
				}
			}

			.order-main-middle {
				background: #F9FCFF;
				border-radius: 10rpx;
				margin-top: 30rpx;
				padding: 30rpx 20rpx;

				.user-message {
					@include flex-between;

					.user-name-box {
						display: flex;
						align-items: center;

						.user-icon {
							width: 32rpx;
							height: 32rpx;

							image {
								height: 100%;
							}
						}

						.user-name {
							margin: 0 11rpx 0 14rpx;
							font-size: 26rpx;
							color: #23252B;
							font-weight: bold;
						}

						.user-phone {
							font-size: 24rpx;
							color: $color
						}
					}

					.order-user-code {
						font-size: 24rpx;
						color: #000008;
					}
				}

				.user-address-box {
					@include flex-between;
					margin-top: 20rpx;

					.user-address {
						width: 486rpx;
						font-size: 24rpx;
						color: #23252B;
						line-height: 36rpx;
					}

					.navigator-icon {
						width: 43rpx;
						height: 45rpx;

						image {
							height: 100%;
						}
					}
				}
			}

			.sending-message-box {
				@include flex-between;
				font-size: 22rpx;
				color: #23252B;
				margin-top: 20rpx;
			}
			.sending-message-box:last-of-type {
				border-bottom: 1rpx solid #F5F5F5;
				padding-bottom: 26rpx;
			}
		}

		.detail-tool-box {
			@include flex-between;
			padding: 20rpx 30rpx;

			.navi-left {
				font-size: 22rpx;
				color: $color;
			}

			.navi-right {
				.iconfont {
					font-size: 23rpx;
					color: #B0B4BA;
				}
			}

			.status-btn-box {
				display: flex;

				.status-btn {
					@include flex-center;
					width: 150rpx;
					height: 56rpx;
					border-radius: 28rpx;
					font-size: 24rpx;
					margin-left: 30rpx;
				}

				.status-btn_normal {
					background: #FFFFFF;
					border: 1px solid #DEDEDE;
					color: #23252B;
				}

				.status-btn_success {
					background: #00A6E2;
					color: #FFFFFF;
				}
			}
		}
	}
</style>
